import React, {useState, useEffect} from 'react';
import PropTypes from 'prop-types'
import {connect} from 'react-redux';
import {Form, Select, Input, Col, Row} from 'antd'
import AudioCategorySelect from "./audio.category.select";
import AudioTypeSelect from "./audio.type.select";
import AudioLanguageSelect from "./audio.language.select";
import AudioFileSelect from "./audio-file.select";


/**
 * @typedef initialValueBankAudio
 * @property {string} identifier
 * @property {File} file
 * @property {string | number} language_id
 * @property {string | number} type_id
 */
/**
 * @typedef optionsForm
 * @property {'vertical'|'horizontal'} layout
 * @property {'small' | 'middle' | 'large' | undefined} size
 */

function mapStateToProps(state) {
    return {};
}


/**
 * @param {object} props
 * @param {optionsForm} props.options
 * @param {'ADD' | 'EDIT'} props.mode
 * @param {initialValueBankAudio} props.initialValue
 * @param props.form
 * @returns {JSX.Element}
 */
function AnnouncementForm(props) {
    const [newForm] = Form.useForm()

    let {form = newForm, options = {layout: 'vertical'}, mode = 'ADD', initialValue = {}} = props


    const [intVal, setIntVal] = useState(() => {
        if (initialValue) return initialValue
        return {}
    })

    useEffect(() => {
        if (mode === 'EDIT') {
            setIntVal({
                ...initialValue,
            })
        }
    }, [mode, initialValue])


    return (
        <Form
            {...options}
            form={form}
            initialValue={intVal ?? initialValue}
        >
            <Row gutter={24}>
                <Col xs={24} lg={12}>
                    <Form.Item name={'identifier'} label={'Identifier'}
                               rules={[{required: true, message: "can't be empty!"}]}>
                        <Input/>
                    </Form.Item>
                    <Form.Item name={'title'} label={'Title'}
                               rules={[{required: true, message: "can't be empty!"}]}>
                        <Input/>
                    </Form.Item>

                    <AudioFileSelect
                        form={form}
                        lang={'id'}
                        label={'Audio File Indonesia'}
                        rules={[{required:true,message:"Can't be empty!"}]}/>

                    <AudioFileSelect
                        form={form}
                        lang={'en'}
                        label={'Audio File English'}
                        rules={[{required:true,message:"Can't be empty!"}]}/>

                    <AudioFileSelect
                        form={form}
                        lang={'locale'}
                        label={'Audio File Daerah'}
                        rules={[{required:true,message:"Can't be empty!"}]}/>
                </Col>
            </Row>
        </Form>
    );
}


AnnouncementForm.defaultProps = {
    options: {
        layout: "vertical",
        size: "small"
    },
    form: undefined,
    mode: "ADD",
    initialValue: {}
}
AnnouncementForm.propTypes = {
    options: PropTypes.shape({
        layout: PropTypes.oneOf(['vertical', 'horizontal']).isRequired,
        size: PropTypes.oneOf(['small', 'middle', 'large', undefined])
    }),
    form: PropTypes.any.isRequired,
    mode: PropTypes.oneOf(['ADD', 'EDIT']).isRequired,
    initialValue: PropTypes.shape({
        identifier: PropTypes.string,
        file: PropTypes.any,
        language_id: PropTypes.oneOfType([PropTypes.string, PropTypes.number, PropTypes.any]),
        type_id: PropTypes.oneOfType([PropTypes.string, PropTypes.number, PropTypes.any])

    })
}
export default connect(
    mapStateToProps,
)(AnnouncementForm);