import React, {useState} from 'react';
import { connect } from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import {SaveOutlined} from "@ant-design/icons";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {Form} from "antd";
import AnnouncementTab from "../components/tabs/announcement.tab";

function mapStateToProps(state) {
    return {

    };
}

function Index()  {

    const [loading, setLoading] = useState(false)
    const [ form ] = Form.useForm();
    const onFinish = ()=> {
        setLoading(true)
        form.validateFields()
            .then((value)=> {
                console.log(value)
                // setLoading(false)
            })
            .catch((err)=> {
                setLoading(false)
            })
    }

    return (
        <>
            <HeroComponents
                loading={loading ?? false}
                name={"Create"}
                positionName={'General | Audio Announcement'}
                thumbnail={null}
                positionColor={'cyan'}
                extra={<>
                    <ButtonPermission
                        permission={['admin']}
                        theme={{
                            type: "notification",
                            property: {
                                message: "Access Denied",
                                description: "Anda tidak dapat menyimpan ke database "
                            }
                        }}
                        config={{
                            icon: <SaveOutlined />,
                            type: "primary",
                            danger:false,
                            loading:loading ?? false,
                            ghost:true,
                            size: "small",
                            onClick: onFinish,
                        }}>
                        <span>Save</span>
                    </ButtonPermission>
                </>}
            />

            <div style={{marginTop:90}}>
                <ContainerTailwind>
                    <AnnouncementTab form={form}/>
                </ContainerTailwind>
            </div>
        </>
    );
}

export default connect(
    mapStateToProps,
)(Index);