import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Form, Select } from 'antd';
import ApiService from "../../../../../../../../services/api.service";
import {findKey} from "../../../../../../../../utils";

function mapStateToProps(state) {
 return {

 };
}

/**
 *
 * @param {object} props
 * @param {string | string[]} props.name
 * @param {'id' | 'en' | 'locale'} props.lang
 * @param {string} props.label
 * @param props.form
 * @param {Array | object[]} props.rules
 * @param {string[]} props.selectedValue
 * @param {string[]} props.selectedLabel
 * @returns {JSX.Element}
 */
function AudioFileSelect(props = {})  {
    let { form, name, label, rules, selectedValue,selectedLabel } = props
    const [ loading, setLoading ] = useState(false)
    const [ data, setData ] = useState([])
    const [ params, setParams] = useState({
        page:1,
        limit:100
    })

    useEffect(()=> {
        setLoading(true)
        new ApiService({
            url:`/api/v1/audio`,
            config : {
                params: {
                    ...params
                }
            }
        }).get()
            .then((response)=> {
                setData([...response?.data])
                setLoading(false)
            })
            .catch((err)=> {
                setData([ ])
                setLoading(false)
            })
    },[params])
  return (
   <Form.Item name={name ?? "audio"} label={label} rules={rules ?? []}>
       <Select loading={loading} showSearch>
           {
               data.map((item,index)=> (
                   <Select.Option value={findKey(item,selectedValue)} key={findKey(item,selectedValue)}>{findKey(item,selectedLabel)}</Select.Option>
               ))
           }
       </Select>
   </Form.Item>
  );
}

export default connect(
 mapStateToProps,
)(AudioFileSelect);