import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Form, Select } from 'antd';
import ApiService from "../../../../../../../../services/api.service";
import {findKey} from "../../../../../../../../utils";
function mapStateToProps(state) {
    return {

    };
}

/**
 * @typedef RulesForm
 * @property {boolean} required
 * @property {string | "Can't be empty"} message
 */

/**
 *
 * @param {object} props
 * @param {object} props.select
 * @param {string | Array | [] | string[] } props.name
 * @param {object } props.form
 * @param {string | 'Audio Category' } props.label
 * @param {RulesForm[]} props.rules
 * @param {function} props.children
 * @param {string[] | string} props.selectValue
 * @param {string[] | string} props.selectLabel
 * @returns {JSX.Element}
 */
function AudioTypeSelect(props)  {
    let { name, label, rules,selectValue,selectLabel } = props
    const [ data, setData ] = useState([])
    const [ loading, setLoading ] = useState(false)
    const [ params ] = useState({
        page:1,
        limit:100
    })
    const [pagination, setPagination ] = useState({
        page:1,
        limit:50
    })
    
    useEffect(()=> {
        setLoading(true)
        new ApiService({
            url:`/api/v1/audio/type`,
            config: {
                params: {
                    ...params,
                }
            }
        }).get()
            .then((response)=> {
                setData([...response?.data])
                setPagination({
                    ...pagination,
                    ...response?.pagination,
                    ...response?.paginate
                })
                setLoading(false)
            })
            .catch((err)=> {
                setLoading(false)
                setData([])

            })
    },[params])
    
    return (
        <Form.Item name={name ?? 'type_id'} label={label ?? 'Type'} rules={rules ?? []} {...props?.form}>
            <Select loading={loading} {...props?.select}>
                {
                    Array.isArray(data) && data.length > 0 ?
                      data.map((item,index)=> {
                          return !props?.children ?
                            (
                              <Select.Option key={findKey(item,selectValue)} value={findKey(item,selectValue)}>{findKey(item,selectLabel)}</Select.Option>
                            )
                            : typeof(props?.children) === 'function' ? props?.children({item,index}) : null
                      }): null
                }
            </Select>
        </Form.Item>
    );
}

export default connect(
    mapStateToProps,
)(AudioTypeSelect);